import { useState } from "react";
import { addWeeks, eachDayOfInterval, format, getDate, getDay, isFriday, isMonday, parse, parseISO, startOfToday } from "date-fns";
import Modal from "../../common/Modal";
import useAuthHttpClient from "../../../hooks/useAuthHttpClient";
import { useAuth } from "../../../providers/authProvider";
import { Spinner } from "../../icons/Spinner";
import { useNotification } from "../../../providers/notificationProvider";
import DatePicker from "tailwind-datepicker-react";
import { isEmpty } from "../../../utils";
import RecurrenceView from "./recurrenceView";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}
export default function AddEventModal({ open, setOpen, setting, events, setEvents }) {
  const { user } = useAuth();
  const [details, setDetails] = useState([])
  const [color, setColor] = useState('')

  const [name, setName] = useState('')
  const [date, setDate] = useState(''); //date
  const [from, setFrom] = useState(); //from time
  const [to, setTo] = useState(); //to time
  const [itemType, setType] = useState("No"); // Matiere, Item
  const [uploading, setUploading] = useState(false);
  // Recurrence View
  const [yesData, setYesData] = useState({
    kind: "Daily", //Daily, Weekly, Monthly
    // Daily start
    daily: "Every", //Every, Everyweekly
    dailycount: 1, //1~6
    // Weekly start
    weeklylist: ["Monday"], //Monday~Sunday
    //Monthly start
    monthlyday: 1, //1~30
    monthlycount: 1, //1~4
    fromdate: "", // recurrence start date
    todate: "", // recurrence end date
  });

  let tempDetails = [], tempColors = []

  if (!isEmpty(setting)) {
    tempDetails = setting.details.replace(/\s/g, "").split(",")
    tempColors = setting.colors.replace(/\s/g, "").split(",")
  }

  const authHttpClient = useAuthHttpClient();
  const { showNotification } = useNotification();

  const handleDetail = (value) => {
    const tempDetails = [...details]
    const index = tempDetails.indexOf(value)
    if (index !== -1) {
      tempDetails.splice(index, 1)
    }
    else {
      tempDetails.push(value)
    }
    setDetails(tempDetails)
  }

  const addEvent = (event) => {
    const tempEvents = [...events];
    event.forEach(singleEvent => {
      tempEvents.push(parseEvent(singleEvent));
    });
    setEvents(tempEvents);
  };

  const parseEvent = (event) => ({
    _id: event._id,
    user_id: event.user_id,
    name: event.name,
    prename: event.prename,
    date: format(new Date(event.from), "yyyy-MM-dd"),
    from: format(new Date(event.from), "HH:mm"),
    to: format(new Date(event.to), "HH:mm"),
    details: event.details,
    color: event.color
  });

  const handleClick = () => {
    if (itemType === "No") {
      console.log("No:", itemType)
      if (validateNo()) {
        submitNo();
      }
    }
    else if (itemType === "Yes") {
      console.log("Yes:", itemType)
      if (validateYes()) {
        console.log("-----------------Validate_yes_success-----")
        submitYes();
      }
      // submitYes();
    }
  };

  const dayToNum = () => {
    let tempArray = [];
    yesData.weeklylist.forEach(item => {
      switch (item) {
        case 'Sunday':
          tempArray.push(0)
          break;
        case 'Monday':
          tempArray.push(1)
          break;
        case 'Tuesday':
          tempArray.push(2)
          break;
        case 'Wednesday':
          tempArray.push(3)
          break;
        case 'Thursday':
          tempArray.push(4)
          break;
        case 'Friday':
          tempArray.push(5)
          break;
        default:
          tempArray.push(6);
      }
    })
    console.log("tempArray_weeklylist:", tempArray)
    return tempArray
  }

  const validateNo = (item) => {
    const regexForDate = /^\d{4}-\d{2}-\d{2}$/;
    const regexForTime = /^(0[0-9]|1[0-9]|2[0-3]):([0-5][0-9])$/;
    const fromDateTime = new Date(`2000-01-01T${from}:00.000Z`);
    const toDateTime = new Date(`2000-01-01T${to}:00.000Z`);
    if (isEmpty(name)) {
      showNotification("Pelase enter event name!")
      return false
    }
    if (item === "No") {
      if (!regexForDate.test(date)) {
        showNotification("Date input is not in correct format!");
        return false;
      }
    }
    if (!regexForTime.test(from) || !regexForTime.test(to)) {
      showNotification("Time input is not in correct format!");
      return false;
    }
    if (toDateTime <= fromDateTime) {
      showNotification("Please select the time correctly!");
      return false;
    }
    if (isEmpty(details)) {
      showNotification("Please select the details!")
      return false;
    }
    if (isEmpty(color)) {
      showNotification("Please select the color")
      return false
    }
    return true;
  };

  const validateYes = () => {
    const regexForDate = /^\d{4}-\d{2}-\d{2}$/;
    const regexForTime = /^(0[0-9]|1[0-9]|2[0-3]):([0-5][0-9])$/;
    const fromDateTime = new Date(`2000-01-01T${from}:00.000Z`);
    const toDateTime = new Date(`2000-01-01T${to}:00.000Z`);
    if (isEmpty(name)) {
      showNotification("Pelase enter event name!")
      return false
    }
    if (!regexForTime.test(from) || !regexForTime.test(to)) {
      showNotification("Time input is not in correct format!");
      return false;
    }
    if (toDateTime <= fromDateTime) {
      showNotification("Please select the time correctly!");
      return false;
    }
    if (isEmpty(details)) {
      showNotification("Please select the details!")
      return false;
    }
    if (isEmpty(color)) {
      showNotification("Please select the color")
      return false
    }
    //Daily
    if (yesData.kind === "Daily") {
      console.log("Daily")
    }
    //Weekly
    if (yesData.kind === "Weekly") {
      console.log("Weekly")
      if (isEmpty(yesData.weeklylist)) {
        showNotification("Please select days!")
        return false
      }
    }
    //Monthly
    if (yesData.kind === "Monthly") {
      console.log("Monthly")
    }
    if (!regexForDate.test(yesData.fromdate) || !regexForDate.test(yesData.todate)) {
      showNotification("Please select the recurrence date!")
      return false
    }
    if (yesData.fromdate > yesData.todate) {
      showNotification("Please selec the currence date correctly!")
      return false
    }
    return true;
  };

  const submitNo = async () => {
    setUploading(true);
    try {
      let detailStr = details.join(", ")
      let nameStr = "<Family>" + name + `<${detailStr}>`;
      let insertArray = [];

      let tempData = {
        name: nameStr,
        prename: name,
        user_id: user._id,
        from: parse(`${date}:${from}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
        to: parse(`${date}:${to}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
        details: detailStr,
        color: color
      }
      insertArray.push(tempData)
      const response = await authHttpClient.post("/schedule/", insertArray);
      console.log("InsearArray_Add_Response:", response.data)

      try {
        let insertEmail = {
          emails: setting.attendees.replace(/\s/g, "").split(","),
          title: nameStr,
          content: `The meeting was created on ${date} from ${from} to ${to}.`
        }
        const emailresponse = await authHttpClient.post("/schedule/sendmail", insertEmail);
        console.log("Sendemail_No_Response:", emailresponse.data)
      } catch (err) {
        console.log("sendemail_no_error:", err)
      }

      showNotification("Event successfully added!");
      addEvent(response.data.data);
      setOpen(false);
      setUploading(false);
    } catch (err) {
      console.log(err);
      setUploading(false);
    }
  };

  const submitYes = async () => {
    setUploading(true);
    try {
      console.log("general_setting:", name, from, to, details, color)
      console.log("======submitYes:", yesData)
      let detailStr = details.join(", ")
      let nameStr = "<Family>" + name + `<${detailStr}>`;
      let insertArray = [];

      let today = startOfToday();
      const fromdate = parseISO(yesData.fromdate)
      const todate = parseISO(yesData.todate)
      const intervalArray = eachDayOfInterval({ start: fromdate, end: todate })
      console.log("===Today:", today)
      // Daily
      if (yesData.kind === "Daily") {
        if (yesData.daily === "Every") {
          const filtereddates = intervalArray.filter((_, index) => index % yesData.dailycount === 0);
          console.log("filtereddates:", filtereddates)
          filtereddates.forEach(item => {
            let tempdate = format(new Date(item), "yyyy-MM-dd")
            console.log("tempdate:", tempdate)
            let tempData = {
              name: nameStr,
              prename: name,
              user_id: user._id,
              from: parse(`${tempdate}:${from}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
              to: parse(`${tempdate}:${to}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
              details: detailStr,
              color: color
            }
            insertArray.push(tempData)
          })
        }
        else if (yesData.daily === "Everyweekday") {
          const filtereddates = intervalArray.filter(date => {
            const dayOfWeek = date.getDay();
            return dayOfWeek >= 1 && dayOfWeek <= 5; // Monday: 1, Friday: 5
          });
          filtereddates.forEach(item => {
            let tempdate = format(new Date(item), "yyyy-MM-dd")
            let tempData = {
              name: nameStr,
              prename: name,
              user_id: user._id,
              from: parse(`${tempdate}:${from}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
              to: parse(`${tempdate}:${to}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
              details: detailStr,
              color: color
            }
            insertArray.push(tempData)
          })
        }
      }
      if (yesData.kind === "Weekly") {
        const weeklylistWithNumber = dayToNum();
        const filtereddates = intervalArray.filter((item) => weeklylistWithNumber.includes(getDay(item)));
        filtereddates.forEach(item => {
          let tempdate = format(new Date(item), "yyyy-MM-dd")
          let tempData = {
            name: nameStr,
            prename: name,
            user_id: user._id,
            from: parse(`${tempdate}:${from}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
            to: parse(`${tempdate}:${to}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
            details: detailStr,
            color: color
          }
          insertArray.push(tempData)
        })
      }
      if (yesData.kind === "Monthly") {
        const tempdates = intervalArray.filter((item) => getDate(item) === Number(yesData.monthlyday))
        const filtereddates = tempdates.filter((_, index) => index % yesData.monthlycount === 0)
        filtereddates.forEach(item => {
          let tempdate = format(new Date(item), "yyyy-MM-dd")
          let tempData = {
            name: nameStr,
            prename: name,
            user_id: user._id,
            from: parse(`${tempdate}:${from}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
            to: parse(`${tempdate}:${to}`, "yyyy-MM-dd:HH:mm", new Date()).toISOString(),
            details: detailStr,
            color: color
          }
          insertArray.push(tempData)
        })
      }
      if (isEmpty(insertArray)) {
        showNotification("Please select items correctly!")
      }
      const response = await authHttpClient.post("/schedule/", insertArray);

      try {
        let insertEmail = {
          emails: setting.attendees.replace(/\s/g, "").split(","),
          title: nameStr,
          content: `The meeting was created from ${from} to ${to} between ${fromdate} and ${todate}.`
        }
        const emailresponse = await authHttpClient.post("/schedule/sendmail", insertEmail);
        console.log("Sendemail_Yes_Response:", emailresponse.data)
      } catch (err) {
        console.log("sendemail_yes_error:", err)
      }

      showNotification("Event successfully added!");
      addEvent(response.data.data);
      setOpen(false);
      setUploading(false);
    } catch (err) {
      console.log(err);
      setUploading(false);
    }
  };



  const [show, setShow] = useState(false);
  const options = {
    autoHide: true,
    todayBtn: true,
    clearBtn: true,
    inputDateFormatProp: {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    },
    theme: {
      background: "",
      todayBtn: "bg-primary-600",
      clearBtn: "",
      icons: "",
      text: "",
      disabledText: "",
      input: "",
      inputIcon: "",
      selected: "bg-primary-600",
    },
  };

  return (
    <Modal open={open} setOpen={setOpen}>
      <div className="bg-white rounded-xl p-8 border-2 border-gray-400">
        <div className="col-span-full">
          <label
            htmlFor="name"
            className="block text-lg font-bold leading-6 text-center"
          >
            Add Event
          </label>
        </div>
        <input type="text" className="sr-only" />
        <div className="col-span-full">
          <label
            htmlFor="name"
            className="block text-sm font-medium leading-6"
          >
            Event name
          </label>
          <div className="flex rounded-md ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500">
            <input
              type="text"
              name="name"
              id="name"
              placeholder="Sample"
              value={name}
              onChange={(e) => {
                setName(e.target.value)
              }}
              className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm sm:leading-6 ring-1 rounded-md"
            />
          </div>
        </div>
        {itemType === "No" &&
          <fieldset className="mt-3">
            <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm">
              <div>
                <label htmlFor="card-number" className="text-sm">
                  Date
                </label>
                <DatePicker
                  show={show}
                  setShow={(state) => setShow(state)}
                  options={options}
                  onChange={(value) => {
                    setDate(format(value, "yyyy-MM-dd"));
                  }}
                >
                  <input
                    type="text"
                    name="date"
                    id="date"
                    value={date}
                    onFocus={() => {
                      setShow(true);
                    }}
                    className="relative block w-full rounded-md border-0 bg-transparent py-1.5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:z-10 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6"
                    placeholder={format(Date.now(), "yyyy-MM-dd")}
                    readOnly
                  />
                </DatePicker>
              </div>
            </div>
          </fieldset>
        }
        <fieldset className="mt-3">
          <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm">
            <div className="flex -space-x-px">
              <div className="w-1/2 min-w-0 flex-1">
                <label htmlFor="from" className="text-sm">
                  Start time
                </label>
                <select
                  name="from"
                  id="from"
                  value={from}
                  onChange={(e) => {
                    setFrom(e.target.value);
                  }}
                  className="block w-full rounded-bl-md rounded-tl-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>00:00</option>
                  <option>01:00</option>
                  <option>02:00</option>
                  <option>03:00</option>
                  <option>04:00</option>
                  <option>05:00</option>
                  <option>06:00</option>
                  <option>07:00</option>
                  <option>08:00</option>
                  <option>09:00</option>
                  <option>10:00</option>
                  <option>11:00</option>
                  <option>12:00</option>
                  <option>13:00</option>
                  <option>14:00</option>
                  <option>15:00</option>
                  <option>16:00</option>
                  <option>17:00</option>
                  <option>18:00</option>
                  <option>19:00</option>
                  <option>20:00</option>
                  <option>21:00</option>
                  <option>22:00</option>
                  <option>23:00</option>
                </select>
              </div>
              <div className="min-w-0 flex-1">
                <label htmlFor="to" className="text-sm">
                  End time
                </label>
                <select
                  name="to"
                  id="to"
                  value={to}
                  onChange={(e) => {
                    setTo(e.target.value);
                  }}
                  className="block w-full rounded-br-md rounded-tr-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>00:00</option>
                  <option>01:00</option>
                  <option>02:00</option>
                  <option>03:00</option>
                  <option>04:00</option>
                  <option>05:00</option>
                  <option>06:00</option>
                  <option>07:00</option>
                  <option>08:00</option>
                  <option>09:00</option>
                  <option>10:00</option>
                  <option>11:00</option>
                  <option>12:00</option>
                  <option>13:00</option>
                  <option>14:00</option>
                  <option>15:00</option>
                  <option>16:00</option>
                  <option>17:00</option>
                  <option>18:00</option>
                  <option>19:00</option>
                  <option>20:00</option>
                  <option>21:00</option>
                  <option>22:00</option>
                  <option>23:00</option>
                </select>
              </div>
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-6 bg-white text-center">
          <legend className="block text-sm font-medium leading-6 text-gray-900">
            Recurrence
          </legend>
          <div className="mt-2 mb-4 -space-y-px rounded-md shadow-sm">
            <div className="grid grid-cols-2 border-2 border-primary-600 divide-x-2 divide-primary-600 rounded-lg">
              <div
                onClick={() => setType("No")}
                className={classNames(
                  "text-center hover:cursor-pointer rounded-l block text-sm leading-6 text-gray-900 py-1 px-3",
                  itemType === "No" && "bg-primary-600 text-white"
                )}
              >
                No
              </div>
              <div
                onClick={() => setType("Yes")}
                className={classNames(
                  "hover:cursor-pointer text-center block text-sm leading-6 text-gray-900 py-1 px-3",
                  itemType === "Yes" && "bg-primary-600 text-white"
                )}
              >
                Yes
              </div>
            </div>
            <div>
              {itemType === "Yes" && (
                <RecurrenceView
                  data={yesData}
                  setData={setYesData}
                />
              )}
            </div>
          </div>
        </fieldset>
        <fieldset>
          <legend className="block text-sm font-medium leading-6 text-gray-900">
            Details
          </legend>
          <div className="-space-y-px rounded-md bg-white shadow-sm">
            <div className="bg-gray-100 flex flex-wrap rounded-md">
              {tempDetails.map((item, index) => (
                <div className={`px-4 py-2 flex items-center gap-3 text-${item}-500`} key={index}>
                  <input
                    value={item}
                    onChange={(e) => {
                      handleDetail(e.target.value)
                    }}
                    checked={details?.includes(item)}
                    type="checkbox"
                    className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                  />
                  {item}
                </div>
              ))}
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-3">
          <legend className="block text-sm font-medium leading-6 text-gray-900">
            Colors
          </legend>
          <div className="-space-y-px rounded-md bg-white shadow-sm">
            <div className="bg-gray-100 flex flex-wrap rounded-md">
              {tempColors.map((item, index) => (
                <div className={`px-4 py-2 flex items-center gap-3 text-${item}-500`} key={index}>
                  <input
                    value={item}
                    onChange={(e) => {
                      setColor(e.target.value)
                    }}
                    checked={color === item}
                    type="radio"
                    className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
                  />
                  {item}
                </div>
              ))}
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-2 bg-white">
          <div>
            <button
              onClick={() => {
                handleClick();
              }}
              type="button"
              className="flex mt-8 w-full justify-center rounded-md bg-primary-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-600"
            >
              {uploading ? <Spinner small center /> : "Add"}
            </button>
          </div>
        </fieldset>
      </div>
    </Modal>
  );
}
