import { useEffect, useState } from "react";
import useAuthHttpClient from "../../hooks/useAuthHttpClient";
import { Spinner } from "../../components/icons/Spinner";
import { toast } from "react-toastify";
import { isEmpty } from "../../utils";
import { useSetting } from "../../providers/settingProvider";

export default function Settings() {
  const { setting, changeSetting } = useSetting();
  const authHttpClient = useAuthHttpClient();
  const [user, setUser] = useState({});
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [updating, setUpdating] = useState(false);
  const [changingPassword, setChangingPassword] = useState(false);
  const [attendees, setAttendees] = useState('');
  const [details, setDetails] = useState('');
  const [colors, setColors] = useState([]);
  const [err, seterr] = useState({});

  const handleColor = (value) => {
    const tempColors = [...colors]
    const index = tempColors.indexOf(value)
    if (index !== -1) {
      tempColors.splice(index, 1)
    }
    else {
      tempColors.push(value)
    }
    setColors(tempColors)
  }

  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const userRes = await authHttpClient.get("/auth/getUserInfo")
        setUser(userRes.data.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchUserInfo();
  }, []);

  useEffect(() => {
    if (!isEmpty(setting)) {
      setAttendees(setting.attendees)
      setDetails(setting.details.replace(/\s/g, ""))
      setColors(setting.colors.replace(/\s/g, "").split(","))
    }
  }, [setting])

  const changeSettingInfo = async () => {
    setUpdating(true);
    try {
      let colorsStr = colors.join(',')

      if (isEmpty(details)) {
        setUpdating(false)
        return toast.warning("Please enter details")
      }
      if (isEmpty(colors)) {
        setUpdating(false)
        return toast.warning("Please enter colors")
      }

      const err = await changeSetting({ attendees: attendees.replace(/\s/g, ""), details: details.replace(/\s/g, ""), colors: colorsStr.replace(/\s/g, "") })
      console.log("change_setting_err:", err)
      if (isEmpty(err)) {
        toast.success("Successfully changed!")
      }

      setUpdating(false);
    } catch (error) {
      console.log(error);
      setUpdating(false);
    }
  };

  const changePassword = async () => {
    if (isEmpty(password)) {
      return toast.warning("Please enter current password")
    }
    if (isEmpty(newPassword)) {
      return toast.warning("Please enter new password")
    }
    if (newPassword.length < 8) {
      return toast.warning("Passwords 8+characters")
    }
    if (newPassword !== confirmPassword) {
      seterr({ confirmPassword: "Passwords do not match!" });
      return;
    }
    seterr({});
    setChangingPassword(true);
    try {
      const response = await authHttpClient.post("/auth/changePassword", {
        password,
        newPassword,
      });
      const { error } = response.data;
      error && seterr(error);
      console.log("errror:", error)
      setChangingPassword(false);
      if (isEmpty(error)) {
        toast.success("Successfully changed!")
      }
    } catch (error) {
      console.log(error);
      setChangingPassword(false);
    }
  };

  return (
    <div>
      <main>
        {/* Settings forms */}
        <div className="divide-y divide-white/5">
          <div className="grid max-w-7xl grid-cols-1 gap-x-8 gap-y-10 px-4 py-16 sm:px-6 md:grid-cols-3 lg:px-8">
            <div>
              <h2 className="text-base font-semibold leading-7 ">
                Setting Information
              </h2>
              <p className="mt-1 text-sm leading-6 text-gray-400">
                Use email address that you can send message
              </p>
            </div>

            <form className="md:col-span-2">
              <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:max-w-xl sm:grid-cols-6">
                <div className="col-span-full">
                  <label
                    htmlFor="attendees"
                    className="block text-sm font-medium leading-6 "
                  >
                    Attendees
                  </label>
                  <div className="mt-2">
                    <div className="flex rounded-md ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500">
                      <input
                        type="text"
                        name="attendees"
                        id="attendees"
                        placeholder="John@gmail.com,anna@yahoo.com"
                        value={attendees}
                        onChange={(e) => {
                          setAttendees(e.target.value)
                        }}
                        className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm sm:leading-6"
                      />
                    </div>
                  </div>
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="details"
                    className="block text-sm font-medium leading-6 "
                  >
                    Details
                  </label>
                  <div className="mt-2">
                    <div className="flex rounded-md ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500">
                      <input
                        type="text"
                        name="details"
                        id="details"
                        placeholder="AAA,BBB,CCC"
                        value={details}
                        onChange={(e) => {
                          setDetails(e.target.value)
                        }}
                        className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm sm:leading-6"
                      />
                    </div>
                  </div>
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="colors"
                    className="block text-sm font-medium leading-6 "
                  >
                    Colors
                  </label>
                  <div className="mt-2 bg-gray-100 flex flex-wrap  rounded-md">
                    <div className="px-4 py-2 flex items-center gap-3 text-green-500">
                      <input
                        value="green"
                        onChange={(e) => {
                          handleColor(e.target.value)
                        }}
                        checked={colors?.includes('green')}
                        type="checkbox"
                        className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                      />
                      Green
                    </div>
                    <div className="px-4 py-2 flex items-center gap-3 text-red-500">
                      <input
                        value="red"
                        onChange={(e) => {
                          handleColor(e.target.value)
                        }}
                        checked={colors?.includes('red')}
                        type="checkbox"
                        className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                      />
                      Red
                    </div>
                    <div className="px-4 py-2 flex items-center gap-3 text-yellow-400">
                      <input
                        value="yellow"
                        onChange={(e) => {
                          handleColor(e.target.value)
                        }}
                        checked={colors?.includes('yellow')}
                        type="checkbox"
                        className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                      />
                      Yellow
                    </div>
                    <div className="px-4 py-2 flex items-center gap-3 text-blue-500">
                      <input
                        value="blue"
                        onChange={(e) => {
                          handleColor(e.target.value)
                        }}
                        checked={colors?.includes('blue')}
                        type="checkbox"
                        className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                      />
                      Blue
                    </div>
                  </div>
                </div>
              </div>

              <div className="mt-8 flex">
                <button
                  onClick={() => {
                    changeSettingInfo();
                  }}
                  type="button"
                  className="rounded-md text-white bg-primary-600 px-3 py-2 text-sm font-semibold ring-gray-700 shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                >
                  {updating ? <Spinner small center /> : "Save"}
                </button>
              </div>
            </form>
          </div>

          <div className="grid max-w-7xl grid-cols-1 gap-x-8 gap-y-10 px-4 py-16 sm:px-6 md:grid-cols-3 lg:px-8">
            <div>
              <h2 className="text-base font-semibold leading-7 ">
                Change password
              </h2>
              <p className="mt-1 text-sm leading-6 text-gray-400">
                Update your password associated with your account.
              </p>
              <div className="col-span-full">
                <label
                  htmlFor="email"
                  className="block text-sm font-medium leading-6 "
                >
                  Email address: {user.email}
                </label>
              </div>
            </div>

            <form className="md:col-span-2">
              <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:max-w-xl sm:grid-cols-6">
                <div className="col-span-full">
                  <label
                    htmlFor="current-password"
                    className="block text-sm font-medium leading-6 "
                  >
                    Current password
                  </label>
                  <div className="mt-2">
                    <input
                      id="current-password"
                      name="current_password"
                      type="password"
                      value={password}
                      onChange={(e) => {
                        setPassword(e.target.value);
                      }}
                      autoComplete="current-password"
                      className="block w-full rounded-md border-0 py-1.5 ring-gray-700 shadow-sm ring-1 ring-inset focus:ring-2 focus:ring-inset focus:ring-primary-500 sm:text-sm sm:leading-6"
                    />
                  </div>
                  {err?.password && (
                    <p className="mt-2 text-sm text-red-600" id="email-error">
                      {err?.password}
                    </p>
                  )}
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="new-password"
                    className="block text-sm font-medium leading-6 "
                  >
                    New password
                  </label>
                  <div className="mt-2">
                    <input
                      id="new-password"
                      name="new_password"
                      type="password"
                      value={newPassword}
                      onChange={(e) => {
                        setNewPassword(e.target.value);
                      }}
                      autoComplete="new-password"
                      className="block w-full rounded-md ring-gray-700 border-0 py-1.5 shadow-sm ring-1 ring-inset focus:ring-2 focus:ring-inset focus:ring-primary-500 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="confirm-password"
                    className="block text-sm font-medium leading-6 "
                  >
                    Confirm password
                  </label>
                  <div className="mt-2">
                    <input
                      id="confirm-password"
                      name="confirm_password"
                      type="password"
                      value={confirmPassword}
                      onChange={(e) => {
                        setConfirmPassword(e.target.value);
                      }}
                      autoComplete="new-password"
                      className="block w-full rounded-md border-0 py-1.5 ring-gray-700 shadow-sm ring-1 ring-inset focus:ring-2 focus:ring-inset focus:ring-primary-500 sm:text-sm sm:leading-6"
                    />
                  </div>
                  {err?.confirmPassword && (
                    <p
                      className="mt-2 text-sm text-red-600 inline"
                      id="email-error"
                    >
                      {err?.confirmPassword}
                    </p>
                  )}
                </div>
              </div>

              <div className="mt-8 flex">
                <button
                  type="button"
                  onClick={() => {
                    changePassword();
                  }}
                  className="rounded-md text-white bg-primary-600 px-3 py-2 text-sm font-semibold  shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                >
                  {changingPassword ? <Spinner small center /> : "Save"}
                </button>
              </div>
            </form>
          </div>

        </div>
      </main>
    </div>
  );
}
