import { useState } from "react";
import { format, parse } from "date-fns";
import DatePicker from "tailwind-datepicker-react";

export default function RecurrenceView({ data, setData }) {
  const tempWeeklyList = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]

  const [fromshow, setFromShow] = useState(false);
  const [toshow, setToShow] = useState(false);

  const handleWeekly = (value) => {
    const tempWeekly = [...data.weeklylist]
    const index = tempWeekly.indexOf(value)
    if (index !== -1) {
      tempWeekly.splice(index, 1)
    }
    else {
      tempWeekly.push(value)
    }
    // setWeeklyList(tempWeekly)
    setData({...data, weeklylist: tempWeekly})
  }

  const options = {
    autoHide: true,
    todayBtn: true,
    clearBtn: true,
    inputDateFormatProp: {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    },
    theme: {
      background: "",
      todayBtn: "bg-primary-600",
      clearBtn: "",
      icons: "",
      text: "",
      disabledText: "",
      input: "",
      inputIcon: "",
      selected: "bg-primary-600",
    },
  };

  return (
    <>
      <fieldset className="mt-2 border-gray-400 border-[1px] rounded-md">
        <legend className="block text-sm font-medium leading-6 text-gray-900">
          Recurrence pattern
        </legend>
        <div className="p-2 -space-y-px rounded-md bg-white shadow-sm flex flex-wrap">
          <div className="p-2">
            <div className="flex items-center">
              <input
                name="history"
                type="radio"
                checked={data.kind === "Daily"}
                onChange={() => setData({...data, kind: "Daily"})}
                className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
              />
              <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                Daily
              </label>
            </div>
            <div className="flex items-center">
              <input
                name="history"
                type="radio"
                checked={data.kind === "Weekly"}
                onChange={() => setData({...data, kind: "Weekly"})}
                className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
              />
              <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                Weekly
              </label>
            </div>
            <div className="flex items-center">
              <input
                name="history"
                type="radio"
                checked={data.kind === "Monthly"}
                onChange={() => setData({...data, kind: "Monthly"})}
                className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
              />
              <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                Monthly
              </label>
            </div>
          </div>
          <div className="border-l border-gray-300 h-auto"></div>
          {data.kind === "Daily" &&
            <div className="p-2">
              <div className="flex items-center">
                <input
                  name="everyday"
                  type="radio"
                  checked={data.daily === "Every"}
                  onChange={() => setData({...data, daily: "Every"})}
                  className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
                />
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  Every
                </label>
                <div className="flex ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500 ml-2">
                  <input
                    disabled={data.daily !== "Every"}
                    type="number"
                    name="dailycount"
                    id="dailycount"
                    max={31}
                    min={1}
                    value={data.dailycount}
                    onChange={(e) => {
                      setData({...data, dailycount: e.target.value})
                    }}
                    className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm ring-1 w-20"
                  />
                </div>
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  day(s)
                </label>
              </div>
              <div className="flex items-center">
                <input
                  name="Everyweekday"
                  type="radio"
                  checked={data.daily === "Everyweekday"}
                  onChange={() => setData({...data, daily: "Everyweekday"})}
                  className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
                />
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  Every weekday
                </label>
              </div>
            </div>
          }
          {data.kind === "Weekly" &&
            <div className="items-center">
              {tempWeeklyList.map((item, index) => (
                <div className={`px-4 py-2 flex items-center gap-3 text-${item}-500`} key={index}>
                  <input
                    value={item}
                    onChange={(e) => {
                      handleWeekly(e.target.value)
                    }}
                    checked={data.weeklylist?.includes(item)}
                    type="checkbox"
                    className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                  />
                  {item}
                </div>
              ))}
            </div>
          }
          {data.kind === "Monthly" &&
            <div className="p-2">
              <div className="flex items-center">
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  Day
                </label>
                <div className="flex ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500 ml-2">
                  <input
                    type="number"
                    name="monthlyday"
                    id="monthlyday"
                    max={31}
                    min={1}
                    value={data.monthlyday}
                    onChange={(e) => {
                      setData({...data, monthlyday: e.target.value})
                    }}
                    className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm ring-1 w-20"
                  />
                </div>
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  of every
                </label>
                <div className="flex ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500 ml-2">
                  <input
                    type="number"
                    name="monthlycount"
                    id="monthlycount"
                    max={12}
                    min={1}
                    value={data.monthlycount}
                    onChange={(e) => {
                      setData({...data, monthlycount: e.target.value})
                    }}
                    className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm ring-1 w-20"
                  />
                </div>
                <label className="ml-2 block text-sm font-medium leading-6 text-gray-900">
                  month(s)
                </label>
              </div>
            </div>
          }
        </div>
      </fieldset>

      <fieldset className="mt-2 border-gray-400 border-[1px] rounded-md">
        <legend className="block text-sm font-medium leading-6 text-gray-900">
          Range of recurrence
        </legend>
        <div className="p-2 -space-y-px rounded-md bg-white shadow-sm flex flex-wrap">
          <div className="p-2">
            <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm text-left flex items-center">
              <label htmlFor="fromdate" className="text-sm mr-2">
                Start
              </label>
              <DatePicker
                show={fromshow}
                setShow={(state) => setFromShow(state)}
                options={options}
                onChange={(value) => {
                  setData({...data, fromdate:format(value, "yyyy-MM-dd")});
                }}
              >
                <input
                  type="text"
                  name="fromdate"
                  id="fromdate"
                  value={data.fromdate}
                  onFocus={() => {
                    setFromShow(true);
                  }}
                  className="relative block w-full rounded-md border-0 bg-transparent py-1.5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:z-10 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6"
                  placeholder={format(Date.now(), "yyyy-MM-dd")}
                  readOnly
                />
              </DatePicker>
            </div>
          </div>
          <div className="border-l border-gray-300 h-auto"></div>
          <div className="p-2">
            <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm text-left flex items-center">
              <label htmlFor="todate" className="text-sm mr-2 whitespace-nowrap">
                End by
              </label>
              <DatePicker
                show={toshow}
                setShow={(state) => setToShow(state)}
                options={options}
                onChange={(value) => {
                  setData({...data, todate:format(value, "yyyy-MM-dd")});
                }}
              >
                <input
                  type="text"
                  name="todate"
                  id="todate"
                  value={data.todate}
                  onFocus={() => {
                    setToShow(true);
                  }}
                  className="relative block w-full rounded-md border-0 bg-transparent py-1.5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:z-10 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6"
                  placeholder={format(Date.now(), "yyyy-MM-dd")}
                  readOnly
                />
              </DatePicker>
            </div>
          </div>
        </div>
      </fieldset>
    </>
  );
}
