import { Link } from "react-router-dom";
import { useAuth } from "../../providers/authProvider";
import { useState } from "react";
import { Spinner } from "../icons/Spinner";

export default function RecoveryPassword() {
  const { resetpassword } = useAuth();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [err, seterr] = useState({});
  const [checkEmail, setCheckEmail] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e) => {
    if (e) {
      e.preventDefault();
      if (password !== password2) {
        seterr({ password2: "Passwords do not match" });
        return;
      } else {
        try {
          setIsLoading(true);
          const err = await resetpassword({ email, password });
          console.log(err)
          if (err) {
            seterr(err);
          } else {
            // navigate("/auth/signin");
            seterr({});
            setCheckEmail(true);
          }
          setIsLoading(false);
        } catch (error) {
          setIsLoading(false);
          console.error(error);
        }
      }
    }
  };

  return (
    <>
      <div className="flex min-h-full flex-1 flex-col justify-center py-8 sm:px-6 lg:px-8">
        <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-[480px]">
          <div className="bg-white px-6 py-8 shadow sm:rounded-lg sm:px-12">
            <div className="sm:mx-auto sm:w-full sm:max-w-md">
              <h2 className="pb-4 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
                Reset password
              </h2>
            </div>
            <form className="space-y-6" action="/" method="POST">
              <div>
                <label
                  htmlFor="email"
                  className="block text-sm font-medium leading-6 text-gray-900"
                >
                  Email address
                </label>
                <div className="mt-2">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    className={`block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6
                    ${
                      err?.email &&
                      "text-red-900 ring-red-300 placeholder:text-red-300"
                    }`}
                    value={email}
                    onChange={(e) => {
                      seterr({});
                      setEmail(e.target.value);
                    }}
                  />
                </div>
                {err?.email && (
                  <p className="mt-2 text-sm text-red-600" id="email-error">
                    {err?.email}
                  </p>
                )}
                {checkEmail && (
                  <p className="mt-2 text-sm text-green-600" id="email-error">
                    Verification link was sent. Please check your email!
                  </p>
                )}
              </div>

              <div>
                <label
                  htmlFor="password"
                  className="block text-sm font-medium leading-6 text-gray-900"
                >
                  New Password
                </label>
                <div className="mt-2">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    autoComplete="current-password"
                    required
                    className={`block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6
                    ${
                      err?.password &&
                      "text-red-900 ring-red-300 placeholder:text-red-300"
                    }`}
                    value={password}
                    onChange={(e) => {
                      seterr({});
                      setPassword(e.target.value);
                    }}
                  />
                </div>
                {err?.password && (
                  <p className="mt-2 text-sm text-red-600" id="email-error">
                    {err?.password}
                  </p>
                )}
              </div>

              <div>
                <label
                  htmlFor="password2"
                  className="block text-sm font-medium leading-6 text-gray-900"
                >
                  Confirm password
                </label>
                <div className="mt-2">
                  <input
                    id="password2"
                    name="password2"
                    type="password"
                    required
                    className={`block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6"
                    ${
                      err?.password2 &&
                      "text-red-900 ring-red-300 placeholder:text-red-300"
                    }`}
                    value={password2}
                    onChange={(e) => {
                      seterr({});
                      setPassword2(e.target.value);
                    }}
                  />
                </div>
                {err?.password2 && (
                  <p className="mt-2 text-sm text-red-600" id="email-error">
                    {err?.password2}
                  </p>
                )}
              </div>

              <div>
                <button
                  type="submit"
                  onClick={(e) => handleSubmit(e)}
                  className="flex w-full justify-center rounded-md bg-primary-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-600"
                >
                  {isLoading ? <Spinner small center /> : "Reset password"}
                </button>
              </div>
            </form>

            <p className="mt-10 text-center text-sm text-gray-500">
              Already have an account?{" "}
              <Link
                to="/auth/signin"
                className="font-semibold leading-6 text-primary-600 hover:text-primary-500"
              >
                Login here
              </Link>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
