import { createContext, useContext, useEffect, useState } from "react";

import { isAxiosError } from "axios";

import useAuthHttpClient from "../hooks/useAuthHttpClient";

const SettingContext = createContext({});

export const SettingProvider = ({ children }) => {
  const authHttpClient = useAuthHttpClient();
  const [setting, setSetting] = useState({});
  
  useEffect(() =>{
    getSetting()
  }, [])

  const getSetting = async () => {
    let err = ""
    try {
      const settingRes = await authHttpClient.get("/auth/getSetting")
      let data = settingRes.data
      if (data.success === true) {
        setSetting(data.data)
      }
    } catch (error) {
      if (isAxiosError(error)) {
        console.log(error);
        err = error.response.data.error;
      } else {
        console.log(error);
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  }
  
  const changeSetting = async (changeData) => {
    let err = ""
    try {
      const settingRes = await authHttpClient.post("/auth/changeSetting", changeData);
      let data = settingRes.data
      console.log("changesetting:", data)
      if (data.success === true) {
        setSetting(data.data)
      }
    } catch (error) {
      if (isAxiosError(error)) {
        console.log(error);
        err = error.response.data.error;
      } else {
        console.log(error);
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  }

  const value = {
    setting,
    getSetting,
    changeSetting
  };
  return <SettingContext.Provider value={value}>{children}</SettingContext.Provider>;
};

export function useSetting() {
  return useContext(SettingContext);
}
