import { useNavigate } from "react-router-dom";

function Dashboard() {
  const navigate = useNavigate();
  const handleClick = () => {
    navigate('/planner')
  }
  return (
    <div className="inset-0 p-4">
      <div>
        <div className="flex justify-center">
          <img
            className="center w-full"
            src="/assets/dashboard.png"
            alt="Une compilation transversale pour comprendre lusage de ce traitement."
          />
        </div>
      </div>
      <div className="grid sm:grid-cols-2 gap-8 mt-5">
        <div
          onClick={handleClick}
          className="rounded-lg bg-gray-100 min-h-80 p-8 click-action hover:cursor-pointer shadow-lg hover:shadow-primary-300">
          <div className="mt-5">
            <div className="px-2 font-bold text-gray-400">Schedule meeting</div>
            <div className="p-2 font-bold text-3xl">
              We provide a flexible and<br />
              convenient environment.
            </div>
          </div>
          <div className="flex justify-center mt-5">
            <img
              className="center"
              src="/assets/schedule1.png"
              alt="Une compilation transversale pour comprendre lusage de ce traitement."
            />
          </div>
        </div>
        <div
          onClick={handleClick}
          className="rounded-lg bg-gray-100 min-h-80 p-8 click-action hover:cursor-pointer shadow-lg hover:shadow-primary-300">
          <div className="flex justify-center">
            <img
              src="/assets/schedule2.png"
              alt="Une compilation transversale pour comprendre lusage de ce traitement."
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
