import { useEffect, useState } from "react";
import { format, parse } from "date-fns";
import Modal from "../../common/Modal";
import useAuthHttpClient from "../../../hooks/useAuthHttpClient";
import { useAuth } from "../../../providers/authProvider";
import { Spinner } from "../../icons/Spinner";
import { useNotification } from "../../../providers/notificationProvider";
import DatePicker from "tailwind-datepicker-react";
import { isEmpty } from "../../../utils";

export default function EditEventModal({ open, setOpen, setting, events, setEvents, selectedEvent }) {
  const { user } = useAuth();
  const [details, setDetails] = useState([])
  const [color, setColor] = useState('')

  const [name, setName] = useState('')
  const [date, setDate] = useState(''); //date
  const [from, setFrom] = useState(); //from time
  const [to, setTo] = useState(); //to time
  const [uploading, setUploading] = useState(false);

  let tempDetails = [], tempColors = []

  useEffect(() => {
    if (!isEmpty(selectedEvent)) {
      setName(selectedEvent.prename)
      setDate(selectedEvent.date)
      setFrom(selectedEvent.from)
      setTo(selectedEvent.to)
      setDetails(selectedEvent.details.replace(/\s/g, "").split(","))
      setColor(selectedEvent.color)
    }
  }, [selectedEvent])

  if (!isEmpty(setting)) {
    tempDetails = setting.details.replace(/\s/g, "").split(",")
    tempColors = setting.colors.replace(/\s/g, "").split(",")
  }

  const authHttpClient = useAuthHttpClient();
  const { showNotification } = useNotification();


  const handleDetail = (value) => {
    const tempDetails = [...details]
    const index = tempDetails.indexOf(value)
    if (index !== -1) {
      tempDetails.splice(index, 1)
    }
    else {
      tempDetails.push(value)
    }
    setDetails(tempDetails)
  }

  const editEvent = (event) => {
    const tempEvents = [...events];
    console.log("tempEvents:", parseEvent(event))
    let changeEvent = parseEvent(event)
    // tempEvents.push(parseEvent(singleEvent));
    const updatedEvents = tempEvents.map(tempEvent => {
      if (tempEvent._id === selectedEvent._id) {
        return { ...tempEvent, ...changeEvent }
      } else {
        return tempEvent
      }
    })
    console.log("updateEvents:", updatedEvents)
    setEvents(updatedEvents);
  };

  const deleteEvent = (event) => {
    const tempEvents = [...events];
    console.log("tempEvents:", event)
    const updatedEvents = tempEvents.filter(item => item._id !== event._id)
    console.log("updateEvents:", updatedEvents)
    setEvents(updatedEvents);
  };

  const parseEvent = (event) => ({
    _id: event._id,
    user_id: event.user_id,
    name: event.name,
    prename: event.prename,
    date: format(new Date(event.from), "yyyy-MM-dd"),
    from: format(new Date(event.from), "HH:mm"),
    to: format(new Date(event.to), "HH:mm"),
    details: event.details,
    color: event.color
  });

  const handleSave = () => {
    if (validate()) {
      submit();
    }
  };

  const handleDelete = async () => {
    const response = await authHttpClient.delete(`/schedule/${selectedEvent._id}`);
    console.log("InsearArray_Delete_Response:", response.data)
    showNotification("Event successfully deleted!");
    deleteEvent(selectedEvent);
    setOpen(false);
  }

  const validate = () => {
    const regexForDate = /^\d{4}-\d{2}-\d{2}$/;
    const regexForTime = /^(0[0-9]|1[0-9]|2[0-3]):([0-5][0-9])$/;
    const fromDateTime = new Date(`2000-01-01T${from}:00.000Z`);
    const toDateTime = new Date(`2000-01-01T${to}:00.000Z`);
    if (isEmpty(name)) {
      showNotification("Pelase enter event name!")
      return false
    }
    if (!regexForDate.test(date)) {
      showNotification("Date input is not in correct format!");
      return false;
    }
    if (!regexForTime.test(from) || !regexForTime.test(to)) {
      showNotification("Time input is not in correct format!");
      return false;
    }
    if (toDateTime <= fromDateTime) {
      showNotification("Please select the time correctly!");
      return false;
    }
    if (isEmpty(details)) {
      showNotification("Please select the details!")
      return false;
    }
    if (isEmpty(color)) {
      showNotification("Please select the color")
      return false
    }
    return true;
  };

  const submit = async () => {
    setUploading(true);
    try {
      let detailStr = details.join(", ")
      let nameStr = "<Family>" + name + `<${detailStr}>`;

      let updateData = {
        name: nameStr,
        prename: name,
        user_id: user._id,
        from: parse(`${date}:${from}`, "yyyy-MM-dd:HH:mm", new Date()),
        to: parse(`${date}:${to}`, "yyyy-MM-dd:HH:mm", new Date()),
        details: detailStr,
        color: color
      }

      console.log("updatedData:", updateData, selectedEvent)
      const response = await authHttpClient.put(`/schedule/${selectedEvent._id}`, updateData);
      console.log("InsearArray_Update_Response:", response.data)
      showNotification("Event successfully updated!");
      editEvent(response.data.data);
      setOpen(false);
      setUploading(false);
    } catch (err) {
      console.log(err);
      setUploading(false);
    }
  };

  const [show, setShow] = useState(false);
  const options = {
    autoHide: true,
    todayBtn: true,
    clearBtn: true,
    inputDateFormatProp: {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    },
    theme: {
      background: "",
      todayBtn: "bg-primary-600",
      clearBtn: "",
      icons: "",
      text: "",
      disabledText: "",
      input: "",
      inputIcon: "",
      selected: "bg-primary-600",
    },
  };

  return (
    <Modal open={open} setOpen={setOpen}>
      <div className="bg-white rounded-xl p-8 border-2 border-gray-400">
        <div className="col-span-full">
          <label
            htmlFor="name"
            className="block text-lg font-bold leading-6 text-center"
          >
            Edit Event
          </label>
        </div>
        <input type="text" className="sr-only" />
        <div className="col-span-full">
          <label
            htmlFor="name"
            className="block text-sm font-medium leading-6"
          >
            Event name
          </label>
          <div className="flex rounded-md ring-1 ring-gray-700 focus-within:ring-2 focus-within:ring-inset focus-within:ring-primary-500">
            <input
              type="text"
              name="name"
              id="name"
              placeholder="Sample"
              value={name}
              onChange={(e) => {
                setName(e.target.value)
              }}
              className="flex-1 border-0 bg-transparent py-1.5 focus:ring-0 sm:text-sm sm:leading-6 ring-1 rounded-md"
            />
          </div>
        </div>
        <fieldset className="mt-3">
          <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm">
            <div>
              <label htmlFor="card-number" className="text-sm">
                Date
              </label>
              <DatePicker
                show={show}
                setShow={(state) => setShow(state)}
                options={options}
                onChange={(value) => {
                  setDate(format(value, "yyyy-MM-dd"));
                }}
              >
                <input
                  type="text"
                  name="date"
                  id="date"
                  value={date}
                  className="relative block w-full rounded-md border-0 bg-transparent py-1.5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:z-10 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:text-sm sm:leading-6"
                  placeholder={format(Date.now(), "yyyy-MM-dd")}
                  readOnly
                />
              </DatePicker>
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-3">
          <div className="mt-2 -space-y-px rounded-md bg-white shadow-sm">
            <div className="flex -space-x-px">
              <div className="w-1/2 min-w-0 flex-1">
                <label htmlFor="from" className="text-sm">
                  Start time
                </label>
                <select
                  name="from"
                  id="from"
                  value={from}
                  onChange={(e) => {
                    setFrom(e.target.value);
                  }}
                  className="block w-full rounded-bl-md rounded-tl-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>00:00</option>
                  <option>01:00</option>
                  <option>02:00</option>
                  <option>03:00</option>
                  <option>04:00</option>
                  <option>05:00</option>
                  <option>06:00</option>
                  <option>07:00</option>
                  <option>08:00</option>
                  <option>09:00</option>
                  <option>10:00</option>
                  <option>11:00</option>
                  <option>12:00</option>
                  <option>13:00</option>
                  <option>14:00</option>
                  <option>15:00</option>
                  <option>16:00</option>
                  <option>17:00</option>
                  <option>18:00</option>
                  <option>19:00</option>
                  <option>20:00</option>
                  <option>21:00</option>
                  <option>22:00</option>
                  <option>23:00</option>
                </select>
              </div>
              <div className="min-w-0 flex-1">
                <label htmlFor="to" className="text-sm">
                  End time
                </label>
                <select
                  name="to"
                  id="to"
                  value={to}
                  onChange={(e) => {
                    setTo(e.target.value);
                  }}
                  className="block w-full rounded-br-md rounded-tr-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-primary-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>00:00</option>
                  <option>01:00</option>
                  <option>02:00</option>
                  <option>03:00</option>
                  <option>04:00</option>
                  <option>05:00</option>
                  <option>06:00</option>
                  <option>07:00</option>
                  <option>08:00</option>
                  <option>09:00</option>
                  <option>10:00</option>
                  <option>11:00</option>
                  <option>12:00</option>
                  <option>13:00</option>
                  <option>14:00</option>
                  <option>15:00</option>
                  <option>16:00</option>
                  <option>17:00</option>
                  <option>18:00</option>
                  <option>19:00</option>
                  <option>20:00</option>
                  <option>21:00</option>
                  <option>22:00</option>
                  <option>23:00</option>
                </select>
              </div>
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-3">
          <legend className="block text-sm font-medium leading-6 text-gray-900">
            Details
          </legend>
          <div className="-space-y-px rounded-md bg-white shadow-sm">
            <div className="bg-gray-100 flex flex-wrap rounded-md">
              {tempDetails.map((item, index) => (
                <div className={`px-4 py-2 flex items-center gap-3 text-${item}-500`} key={index}>
                  <input
                    value={item}
                    onChange={(e) => {
                      handleDetail(e.target.value)
                    }}
                    checked={details?.includes(item)}
                    type="checkbox"
                    className="h-4 w-4 rounded text-primary-600 focus:ring-primary-500"
                  />
                  {item}
                </div>
              ))}
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-3">
          <legend className="block text-sm font-medium leading-6 text-gray-900">
            Colors
          </legend>
          <div className="-space-y-px rounded-md bg-white shadow-sm">
            <div className="bg-gray-100 flex flex-wrap rounded-md">
              {tempColors.map((item, index) => (
                <div className={`px-4 py-2 flex items-center gap-3 text-${item}-500`} key={index}>
                  <input
                    value={item}
                    onChange={(e) => {
                      setColor(e.target.value)
                    }}
                    checked={color === item}
                    type="radio"
                    className="h-4 w-4 border-gray-300 text-primary-600 focus:ring-primary-600"
                  />
                  {item}
                </div>
              ))}
            </div>
          </div>
        </fieldset>
        <fieldset className="mt-8 bg-white">
          <div className="p-1">
            <button
              onClick={() => {
                handleSave();
              }}
              type="button"
              className="flex w-full justify-center rounded-md bg-blue-700 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-600"
            >
              {uploading ? <Spinner small center /> : "Save"}
            </button>
          </div>
          <div className="p-1">
            <button
              onClick={() => {
                handleDelete()
              }}
              type="button"
              className="flex w-full justify-center rounded-md bg-cyan-700 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-600"
            >
              Delete
            </button>
          </div>
          <div className="p-1">
            <button
              onClick={() => {
                setOpen(false);
              }}
              type="button"
              className="flex w-full justify-center rounded-md bg-red-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-primary-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-600"
            >
              Cancel
            </button>
          </div>
        </fieldset>
      </div>
    </Modal>
  );
}
