import { createContext, useContext, useEffect, useState } from "react";

import { isAxiosError } from "axios";
import { httpClient } from "../config/axiosConfig";

import { getAccessToken, setAccessToken } from "../helper/Token";
import { useNavigate } from "react-router-dom";
import useAuthHttpClient from "../hooks/useAuthHttpClient";

const AuthContext = createContext({});

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(JSON.parse(localStorage.getItem("user")));
  const authHttpClient = useAuthHttpClient();
  
  useEffect(() => {
    const token = getAccessToken();
    token &&
      authHttpClient
        .get("auth/refresh")
        .then((res) => {
          setUser(res.data.data.user);
        })
        .catch((e) => e);
  }, []);

  const navigate = useNavigate();
  const login = async ({ email, password }) => {
    let err = "";
    try {
      const { data } = await httpClient.post("/auth/signin", {
        email,
        password,
      });
      setUser(data.user);
      localStorage.setItem("user", JSON.stringify(data.user));
      setAccessToken(data.user.token);
    } catch (error) {
      if (isAxiosError(error)) {
        console.log(error);
        err = error.response.data.error;
      } else {
        console.log(error);
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  };
  const signup = async (signUpData) => {
    let err = "";
    try {
      await httpClient.post("/auth/signup", signUpData);
    } catch (error) {
      if (isAxiosError(error)) {
        err = error.response.data.error;
      } else {
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  };

  const signout = async () => {
    let err = "";
    try {
      // await httpClient.get("/auth/signout");
      setUser(undefined);
      localStorage.removeItem("user");
      setAccessToken("");
      navigate("/");
    } catch (error) {
      if (isAxiosError(error)) {
        err = error.response.data.error;
      } else {
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  };

  const resetpassword = async ({email, password}) => {
    let err = "";
    try {
      await httpClient.post("/auth/resetPassword", {email, password});
    } catch (error) {
      if (isAxiosError(error)) {
        err = error.response?.data.error;
      } else {
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  };

  const verifyEmail = async (token) => {
    let err;
    try {
      await httpClient.post("/auth/verifyEmail", { token });
      navigate("/");
    } catch (error) {
      if (isAxiosError(error)) {
        err = error.response.data.error;
      } else {
        err = "Opps! Something Unexpected happens";
      }
    }
    return err;
  };

  const value = {
    user,
    login,
    signup,
    signout,
    resetpassword,
    verifyEmail,
  };
  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export function useAuth() {
  return useContext(AuthContext);
}
