import {
  BrowserRouter,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { AuthProvider } from "./providers/authProvider";
import { SettingProvider } from "./providers/settingProvider";

import { useAuth } from "./providers/authProvider";

import Signin from "./components/auth/Signin";
import Signup from "./components/auth/Signup";
import VerifyEmail from "./components/auth/VerifyEmail";
import Main from "./pages/main";
import RecoveryPassword from "./components/auth/ForgetPassword";

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'

const ProtectedRoute = () => {
  const { user } = useAuth();
  return user ? <Main /> : <Navigate to="/auth/signin" replace={true} />;
};

function App() {
  // const theme = useSelector((state) => state.auth.theme);
  return (
      <div className="App">
        <BrowserRouter>
          <AuthProvider>
            <SettingProvider>
              <ToastContainer
                position="top-right"
                autoClose={2000}
                hideProgressBar={false}
                newestOnTop
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable={false}
                pauseOnHover
                theme='light'
              />
              <Routes>
                <Route path="/auth/signin" element={<Signin />} />
                <Route path="/auth/signup" element={<Signup />} />
                <Route path="/auth/recoveryPassword" element={<RecoveryPassword />} />
                <Route path="/auth/verifyEmail/:token" element={<VerifyEmail />} />
                <Route path="*" element={<ProtectedRoute />} />
              </Routes>
            </SettingProvider>
          </AuthProvider>
        </BrowserRouter>
      </div>
  );
}

export default App
